@font-face {
  font-family: "Poppins";
  src: url("assets/fonts/poppins-v20-latin-regular.woff2") format("woff2"), url("assets/fonts/poppins-v20-latin-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Poppins";
  src: url("assets/fonts/poppins-v20-latin-600.woff2") format("woff2"), url("assets/fonts/poppins-v20-latin-600.woff") format("woff");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "Rubik";
  src: url("assets/fonts/rubik-v21-latin-500.woff2") format("woff2"), url("assets/fonts/rubik-v21-latin-500.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
:root {
  --bg_1: #8AFFCF;
  --bg_2: #9B4CFF;
  --bg_3: #28272F;
  --bg_4: #ffffff;
  --fg_1: #ffffff;
  --fg_2: #9AA0A6;
  --fg_3: #6B737A;
  --fg_4: #474D52;
  --fg_5: #28272F;
  --Poppins_h2_SB: normal 600 32px/125% Poppins, Arial, Helvetica, sans-serif;
  --Poppins_h3_SB: normal 600 26px Poppins, Arial, Helvetica, sans-serif;
  --Poppins_p2_M: normal 500 20px/120% Poppins, Arial, Helvetica, sans-serif;
  --Poppins_p3_R: normal 400 16px Poppins, Arial, Helvetica, sans-serif;
  --Poppins_p4_SB: normal 600 14px Poppins, Arial, Helvetica, sans-serif;
}

*, *::after, *::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  border: none;
}

.sign-in {
  width: 280px;
  height: 380px;
}
.sign-in h1 {
  font: var(--Poppins_h3_SB);
  text-align: center;
  color: var(--bg_3);
}
.sign-in__button {
  margin: 14px 0 0 0;
  width: 100%;
  height: 40px;
  background: var(--bg_2);
  border-radius: 10px;
  font: var(--Poppins_p4_SB);
  color: var(--fg_1);
}
.sign-in__or {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 7px 0 6px 0;
}
.sign-in__or hr {
  height: 1px;
  background: var(--fg_2);
  width: 100%;
}
.sign-in__or span {
  font: var(--Poppins_p4_SB);
}
.sign-in__log-alt-cont {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.sign-in__log-alt-cont .log-alternative {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border: 2px solid var(--fg_2);
  border-radius: 10px;
  height: 40px;
  width: 100%;
}
.sign-in__log-alt-cont .log-alternative__context {
  font: var(--Poppins_p4_SB);
}
.sign-in__overlay {
  display: none;
}
.sign-in__ellipse-bg-1 {
  display: none;
  height: 315px;
  width: 315px;
}
.sign-in__ellipse-bg-2 {
  display: none;
  height: 60px;
  width: 60px;
}

.sign-in-form {
  display: flex;
  flex-direction: column;
  gap: 7px;
}
.sign-in-form__sub-input {
  display: flex;
  justify-content: space-between;
  font: var(--Poppins_p4_SB);
}
.sign-in-form__sub-input .remember-user {
  display: flex;
  align-items: center;
  gap: 5px;
  width: fit-content;
  font: var(--Poppins_p4_SB);
}
.sign-in-form__sub-input .remember-user input {
  width: 18px;
  height: 18px;
  accent-color: var(--bg_2);
}
.sign-in-form__sub-input .remember-user label {
  color: var(--fg_4);
}
.sign-in-form__sub-input .forgot-password {
  text-decoration: none;
  color: var(--fg_3);
}

.sign-input {
  position: relative;
}
.sign-input label {
  font: var(--Poppins_p4_SB);
  color: var(--fg_4);
}
.sign-input__input-cont {
  display: flex;
  position: relative;
  align-items: center;
}
.sign-input__input-cont input[type=email],
.sign-input__input-cont input[type=password] {
  height: 40.5px;
  width: 100%;
  border-radius: 10px;
  border: 2px solid var(--fg_3);
  padding: 15px;
}
.input-cont__placeholder {
  position: absolute;
  padding-left: 15px;
  font: var(--Poppins_p3_R);
  color: var(--fg_2);
}

.log-in-form__brand-logo {
  display: flex;
  align-items: center;
  width: fit-content;
}
.log-in-form .circle-container {
  display: none;
  top: -57px;
  left: -117px;
  z-index: -1;
}
.log-in-form .circle-container__fill {
  position: absolute;
  width: calc(58px + 40vw);
  height: calc(58px + 40vw);
}
.log-in-form .circle-container__ring {
  position: relative;
  width: calc(68px + 40vw);
  height: calc(68px + 40vw);
  z-index: -1;
}
.log-in-form .circle-container__log-in-images {
  display: none;
}
.log-in-form .circle-container__log-in-context {
  display: none;
}
.log-in-form .circle-container .ellipse {
  display: none;
  position: absolute;
  z-index: 1;
}
.log-in-form .circle-container .ellipse__bg-1 {
  top: calc(36vh - 75px);
  left: 436px;
  width: 18px;
  height: 18px;
}
.log-in-form .circle-container .ellipse__bg-2 {
  top: calc(36vh - 78px);
  left: 485px;
  width: 100px;
  height: 100px;
}
.log-in-form .circle-container .log-in-context {
  display: none;
  flex-direction: column;
  gap: 35px;
  position: absolute;
  top: 36vh;
  left: 250px;
  z-index: 2;
  width: 430px;
  height: 212px;
}
.log-in-form .circle-container .log-in-context__header {
  font: normal 600 28px/125% Poppins, Arial, Helvetica, "sans-serif";
  letter-spacing: 0.09em;
  color: var(--fg_1);
}
.log-in-form .circle-container .log-in-context__body {
  font: normal 500 16px/120% Poppins, Arial, Helvetica, "sans-serif";
  letter-spacing: 0.15em;
  color: var(--fg_2);
}

.signUp {
  display: flex;
  position: absolute;
}
.log-in-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 100vw;
