  bottom: "3px";
  left: "3px";
}
.brand-logo__icon_large {
  display: none;
  position: relative;
  bottom: "4px";
  left: "4px";
}

button {
  width: 120px;
  height: 403.05px;
  font: var(--Poppins_p4_SB);
}
button_bg-1 {
  background: var(--bg_2);
}
button_bg-2 {
